<template>
  <v-container id="dashboard-view" fluid tag="section" max-width="1000">
    <!------SCREEN IMAGE DIALOGUE--->
    <v-dialog v-model="screenView" justify="center">
      <v-card>
        <v-card-title class="text-h5">
          <v-spacer />
          <v-btn fab class="float-right" @click="screenView = false"
            ><v-icon>mdi-close</v-icon></v-btn
          >
        </v-card-title>
        <v-card-text>
          <img
            :src="
            
              loadScreenImage(currentScreen)
            "
            :alt="
              loadScreenImage(currentScreen)
            "
            class="screenImg"
          />
        </v-card-text>
      </v-card>
    </v-dialog>
    <!---END SCREEN IMAGE DIALOGUE--->
    <v-dialog
      v-model="UploadDialogue"
      transition="dialog-top-transition"
       max-width="500"
    >
      <v-card max-width="500">
        <v-toolbar color="primary" dark>
          <v-icon>mdi mdi-warning </v-icon>
          <h3 class="center">Uploading please wait...</h3>
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="reloadPage()"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <div class="container" v-if="showLoader == true">
          <div class="loading-bar">
            <div class="percent" :style="{ width: loaderWidth + '%' }"></div>
          </div>
        </div>
        <v-card-actions class="justify-center">
          <v-btn color="primary"  @click="reloadPage()">
            <h4 class="center" color="white">
              {{ loaderWidth + "%" }}
            </h4>

            <v-icon>mdi-check-outline</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-row justify="center">
      <v-col cols="6">
        <v-btn color="primary" @click="launch = true">
          Create Campaign
        </v-btn>
      </v-col>
       <v-col cols="6">
        <v-btn color="primary"  @click="showDepositBox=!showDepositBox">
           <b class="">Fund Your Wallet</b>
        </v-btn>
      </v-col>
    </v-row>
    <div>
      <v-row justify="center">
        <v-col cols="12" md="12" class="mx-auto">
          <div @click="activeCampaign = !activeCampaign">
            <div class="display-1 font-weight-bold float-left cursor">
              Active & Approved campaigns
              <v-icon v-if="activeCampaign">
                mdi mdi-chevron-up
              </v-icon>
              <v-icon v-else>
                mdi mdi-chevron-down
              </v-icon>
            </div>
            <div class="float-right body-2">
              {{ activeCampaigns.length }} active campaign
            </div>
          </div>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col md="12">
          <!----rejected campaign---->
          <transition
            name="fade"
            v-for="(campaign, index) in rejectedCampaign"
            :key="index"
          >
            <v-card
              v-if="activeCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.media_for_user)"
                    :alt="loadImage(campaign.mediaType, campaign.media_for_user)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.media_for_user)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>
                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="warning"
                    >
                      Rejected::Check your email
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br/><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-btn icon disabled>
                    <v-icon>mdi-television-stop</v-icon>
                  </v-btn>
                  <v-btn icon disabled>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on="on"
                        v-on:click="createCurrentCampaign(campaign)"
                      >
                        <v-icon>mdi-delete</v-icon>
                      </v-btn>
                    </template>
                    <span>Delete Campaign</span>
                  </v-tooltip>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
          <!--------------------------->
          <!------pending approval campaigns----->
          <transition
            name="fade"
            v-for="(campaign, index) in unApprovedCampaigns"
            :key="index"
          >
            <v-card
              v-if="activeCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.media_for_user)"
                    :alt="loadImage(campaign.mediaType, campaign.media_for_user)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.media_for_user)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>
                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                    >
                      Pending Approval...
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br/><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-btn icon disabled>
                    <v-icon>mdi-television-stop</v-icon>
                  </v-btn>
                  <v-btn icon disabled>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on="on"
                        v-on:click="createCurrentCampaign(campaign)"
                      >
                        <v-icon>mdi-delete</v-icon>
                      </v-btn>
                    </template>
                    <span>Delete Campaign</span>
                  </v-tooltip>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
          <!-------------------------->
           <!----for not yet paid campaign----->
          <transition
            name="fade"
            v-for="(campaign, index) in not_paid_campaigns"
            :key="index"
          >
            <v-card
              v-if="activeCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.media_for_user)"
                    :alt="loadImage(campaign.mediaType, campaign.media_for_user)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.media_for_user)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>

                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                    >
                      Insufficient Funds
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br /><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-tooltip right>
                    <template v-slot:activator="{ attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on:click="createArchive(campaign)"
                        title="Archive This Campaign"
                      >
                        <v-icon>mdi-archive</v-icon>
                      </v-btn>
                    </template>
                    <span>Archive this campaign</span>
                  </v-tooltip>
                  <v-btn icon disabled>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-btn icon v-on:click="createCurrentCampaign(campaign)">
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
          <!-------------------------->
          <!----for an active campaign----->
          <transition
            name="fade"
            v-for="(campaign, index) in activeCampaigns"
            :key="index"
          >
            <v-card
              v-if="activeCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.media_for_user)"
                    :alt="loadImage(campaign.mediaType, campaign.media_for_user)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.media_for_user)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>

                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                      @click="dialog = true"
                    >
                      Active:Edit File
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br /><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-btn icon>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-tooltip right>
                    <template v-slot:activator="{ attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on:click="createArchive(campaign)"
                        title="Archive This Campaign"
                      >
                        <v-icon>mdi-archive</v-icon>
                      </v-btn>
                    </template>
                    <span>Campaign Archive</span>
                  </v-tooltip>
                  <v-btn icon>
                    <v-icon v-on:click="createCurrentCampaign(campaign)"
                      >mdi-delete</v-icon
                    >
                  </v-btn>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
          <!-------------->
        </v-col>
      </v-row>
    </div>

    <div class="mt-12">
      <v-row justify="center">
        <v-col cols="12" md="12" class="mx-auto">
          <div @click="archivedCampaign = !archivedCampaign">
            <div class="display-1 font-weight-bold float-left cursor">
              Archived campaigns
              <v-icon v-if="archivedCampaign">
                mdi mdi-chevron-up
              </v-icon>
              <v-icon v-else>
                mdi mdi-chevron-down
              </v-icon>
            </div>
            <div class="float-right body-2">
              {{ archivedCampaigns.length }} archived campaigns
            </div>
          </div>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col
          v-for="(campaign, index) in archivedCampaigns"
          :key="index"
          md="12"
        >
          <transition name="fade">
            <v-card
              v-if="archivedCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.media_for_user)"
                    :alt="loadImage(campaign.mediaType, campaign.media_for_user)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.media_for_user)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>

                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                      v-on:click="launch_archive_campaign(campaign)"
                    >
                      Activate
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br /><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-btn icon disabled>
                    <v-icon>mdi-television-stop</v-icon>
                  </v-btn>
                  <v-btn icon disabled>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on="on"
                        disabled
                        title="Archived Campaign"
                      >
                        <v-icon v-on:click="createCurrentCampaign(campaign)"
                          >mdi-delete</v-icon
                        >
                      </v-btn>
                    </template>
                    <span>Campaign Archive</span>
                  </v-tooltip>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
        </v-col>
      </v-row>
    </div>

    <!--Dialogues -->
    <!---payment dialogue--->
      <v-dialog
        v-model="showDepositBox"
        transition="dialog-top-transition"
        max-width="500"
      >
        <v-card max-width="400">
          <v-toolbar color="primary" dark>
            <v-icon>mdi mdi-warning </v-icon>Fund Your Wallet
            <v-btn
              icon
              absolute
              right
              outlined
              class="float-right"
              color="white"
              @click="showDepositBox = false"
            >
              <v-icon>mdi mdi-close</v-icon>
            </v-btn>
          </v-toolbar>
           <v-col cols="12" md="12">
                <v-row v-if="showDepositBox" class="payDialogue">
                  <v-col md="12" cols="12">
                    <v-text-field
                      dense
                      label="Enter amount without comma"
                      placeholder="Placeholder"
                      outlined
                      v-model.number="depositeAmount"
                      type="number"
                      @change="calculateCharges()"
                    />
                  </v-col>
                  <v-col md="12" cols="12">
                    <p style='text-align:center;'>Number(must start with 237)</p>
                    <p>Charges {{charges}}  FCFA</p>
                  </v-col>
                  <v-col md="12" cols="12">
                    <v-text-field
                      dense
                      label="237652772976"
                      placeholder="Placeholder"
                      outlined
                      v-model="depositeNumber"
                    />
                  </v-col>
                  <v-col md="12" cols="12">
                    <p class="feedback"></p>
                  </v-col>
                  <v-col cols="6">
                    <div class="payBtn1">
                      <v-btn color="primary"  @click="payApp()">
                        <b class="">Proceed</b>
                      </v-btn>
                    </div>
                  </v-col>
                  <v-col cols="6">
                    <div class="payBtn2">
                        <v-btn color="primary"  @click="confirmPayment()">
                        <b class="">Confirm Payment</b>
                      </v-btn>
                    </div>
                  </v-col>
                </v-row>
          </v-col>
        </v-card>
      </v-dialog>
      <!---end payment dialogue--->
    <!--delete dialogue-->
    <v-dialog
      v-model="deleteModal"
      transition="dialog-top-transition"
      max-width="400"
    >
      <v-card max-width="400">
        <v-toolbar color="primary" dark>
          <v-icon>mdi mdi-warning </v-icon> {{ modalText.headerText }}
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="close_delete_modal()"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <p class="pt-4 text-center text-h3">
            {{ modalText.questionText }} <br />
          </p>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn color="primary" @click="deleteCampaign()">
            {{ modalText.actionText }} <v-icon>mdi-check-outline</v-icon>
          </v-btn>
