    <!------------------>
    <v-row justify="center">
      <!--error Dialogue-->
      <v-dialog
        v-model="errorModal"
        transition="dialog-top-transition"
        max-width="400"
      >
        <v-card max-width="400">
          <v-toolbar color="primary" dark>
            <v-icon>mdi mdi-warning </v-icon> {{ modalText.headerText }}
            <v-btn
              icon
              absolute
              right
              outlined
              class="float-right"
              color="white"
              @click="errorModal = false"
            >
              <v-icon>mdi mdi-close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-card-text>
            <p class="pt-4 text-center text-h3">
              {{ modalText.questionText }} <br />
            </p>
          </v-card-text>
        </v-card>
      </v-dialog>
      <!----------------------------------->
        <!-------------Launch dialogue(pay as you go or subscription)------->
      <div>
          <v-dialog v-model="launch" width="500">
            <v-card>
                <v-card-title class="headline">
                  SELECT A PACKAGE
                  <v-btn icon absolute right @click="launch = false">
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </v-card-title>
                <v-divider />
                <v-card-text
                  class="black--text text-justify pt-2 text-32"
                >
                <b>Premium:</b>
                {{lauchText.subscription}}
                </v-card-text>
                <v-card-text
                  class="black--text text-justify pt-2 text-32"
                >
                <b>Entrepreneur:</b>
                {{lauchText.pay_as_you_go}}
                  <div class="planButtons">
                    <v-btn color="#15907b"  @click="premium=true">
                        <b class="">Premium Package</b>
                      </v-btn>
                    <v-btn color="primary" @click="dialog = true">
                      Entrepreneur Package
                    </v-btn>
                  </div>
                </v-card-text>
              
            </v-card>
          </v-dialog>
      </div>
