              <v-toolbar-title>Create a new campaign: Premium Package</v-toolbar-title>
              <v-spacer />
              <v-toolbar-items>
                <v-btn icon dark @click="premium = false">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-toolbar-items>
            </v-toolbar>
