                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-alert-circle-outline
                                </v-icon>
                              </template>
                              <span>
                                Choose a unique name by which you can recognize
                                your campaign if you run multiple campaigns at
                                the same time
                              </span>
                            </v-tooltip>

                            <v-icon color="#15907b" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <v-divider class="my-5 py-1"/>
                      <!-------------------->
                      <div>
                        <v-row>
                          <v-col md="10">
