                              v-on:change="setPremiumData()"
                               color="#15907b"
                            />
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-map
                                </v-icon>
                              </template>
                              <span>
                                Select City
                              </span>
                            </v-tooltip>
                            <v-icon color="#15907b" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <div>
                          <h5 class="planTags">{{campaignInfo.premium_data.weekly_plan!==0?"WEEKLY PLAN:"+campaignInfo.premium_data.weekly_plan+' FCFA':""}}</h5>
                          <h5 class="planTags">{{campaignInfo.premium_data.monthly_plan!==0?"MONTHLY PLAN:"+campaignInfo.premium_data.monthly_plan+' FCFA':""}}</h5>
                      </div>
                      <v-divider class="my-5 py-1" />
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-select
                              :items="premium_plan"
                              label="Select Plan"
                              outlined
                              dense
                              v-model="campaignInfo.premium_data.plan"
                              v-on:change="setPremiumPlan()"
                               color="#15907b"
                            />
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-map
                                </v-icon>
                              </template>
                              <span>
                                Select Plan
                              </span>
                            </v-tooltip>
                            <v-icon color="#15907b" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <v-divider class="my-5 py-1" />
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-btn
                              color="#15907b"
                              block
                              class="text-capitalize font-weight-bold"
                              @click="map = true"
                              v-on:click="reloadMap"
                            >
                              Screens:
                              {{ campaignInfo.numberOfScreens }}
                            </v-btn>
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-alert-circle-outline
                                </v-icon>
                              </template>
                              <span>
                                View the screens available in the current city and their locations
                              </span>
                            </v-tooltip>
                          </v-col>
                        </v-row>
