                              <v-list-item-title>
                                {{ parseInt(campaignInfo.numberOfDays) }}
                              </v-list-item-title>
                              <v-list-item-subtitle>
                                Number of days
                              </v-list-item-subtitle>
                            </v-list-item-content>
                          </v-list-item>
                          <v-divider inset />
                          <v-list-item>
                            <v-list-item-icon>
                              <v-icon color="#15907b">
                                mdi-monitor
                              </v-icon>
                            </v-list-item-icon>

                            <v-list-item-content>
                              <v-list-item-title>
                                {{ campaignInfo.numberOfScreens }}
                              </v-list-item-title>
                              <v-list-item-subtitle>
                                Number of screens
                              </v-list-item-subtitle>
                            </v-list-item-content>
                          </v-list-item>
                          <v-divider inset />
                          <v-list-item>
                            <v-list-item-icon>
                              <v-icon color="#15907b">
                                mdi-image
                              </v-icon>
                            </v-list-item-icon>

                            <v-list-item-content>
                              <v-list-item-title>{{
                                campaignInfo.campaignMedia.name
                              }}</v-list-item-title>
                              <v-list-item-subtitle>
                                Campaign Media
                              </v-list-item-subtitle>
                            </v-list-item-content>
                          </v-list-item>
                          <v-divider inset />
                          <v-list-item>
                            <v-list-item-icon>
                              <v-icon color="#15907b">
                                mdi-cash
                              </v-icon>
                            </v-list-item-icon>

                            <v-list-item-content>
                              <v-list-item-title>{{
                                currency + " " + campaignInfo.campaignCost
                              }}
                              <span class="discountText">{{discount!==0?"Discount: "+discount+"%":""}}</span>
                              </v-list-item-title>
                              <v-list-item-subtitle>
                                Total Cost
                              </v-list-item-subtitle>
                            </v-list-item-content>
                          </v-list-item>
                          <v-list-item>
                             <v-list-item-icon>
                              <v-icon color="#15907b">
                                mdi-money
                              </v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-title>
                                  Coupon Code(optional)
                                </v-list-item-title>
                                 <v-list-item-subtitle>
                                   <v-text-field
                                    dense
                                    label="Enter Coupon Code"
                                    placeholder="Placeholder"
                                    outlined
                                    v-model="coupon"
                                  />
                                 </v-list-item-subtitle>
                                 <p class="couponValidityMessage">{{couponValidityMessage}}</p>
                                  <v-btn color="#15907b" @click="validateCoupon()">{{discountButtonText}}</v-btn>
                            </v-list-item-content>
                          </v-list-item>
                        </v-list>
                      </v-card>

                      <v-btn color="#15907b" @click="e1 = 1">
                        Previous
                      </v-btn>

                      <v-btn text @click="dialog = false">
                        Cancel
                      </v-btn>
                      <v-btn
                        color="#15907b"
                        class="float-right white--text"
                        type="submit"
                      >
                        Submit
                      </v-btn>
                    </v-stepper-content>
                  </v-stepper-items>
                </v-stepper>
              </v-col>
            </v-row>
          </form>
        </v-card>
