        this.errorModal = true;
      }else if(CampaignDuration>25){
        //displays not selected
         this.modalText.questionText ='Duration of video exceeds 20s !';
        this.errorModal = true;
      }else{
        //close form dialogue
        this.dialog = false;
        //upload campaign if no errors
                //create form data object
                //this.activeCampaigns.push(this.campaignInfo);
                let formData = new FormData();
                formData.append("assets", file);
                this.UploadDialogue = true;
                var user = piCredentials.username;
                var password = piCredentials.password;
                var base64encodedData = Buffer.from(user + ":" + password).toString(
                  "base64"
                );
                var config = {
                  method: "post",
                  url: digitadApps.piApp + "/api/files/",
                  headers: {
                    "Content-Type": "multipart/form-data",
                    Authorization: `Basic ${base64encodedData}`,
                  },
                  data: formData,
                };
              
                  axios(config)
                    .then(function(response) {
                      //console.log(JSON.stringify(response.data));
                      if (response.data.success == true) {
                        //console.log(response.data.data);this is data about file
                        let file_name = response.data.data[0].name;
                        let file_info = JSON.stringify({
                          files: [
                            {
                              name: response.data.data[0].name,
                              type: response.data.data[0].type,
                              size: response.data.data[0].size,
                              owner_id: user_id,
                              owner_name: userName,
                            },
                          ],
                          categories: ["string"],
                        });
                        //console.log(file_info);
                        //store file details in pisignage database
                        var config2 = {
                          method: "post",
                          url: digitadApps.piApp + "/api/postupload/",
                          headers: {
                            "Content-Type": "application/json",
                            Authorization: `Basic ${base64encodedData}`,
                          },
                          data: file_info,
                        };

                        axios(config2)
                          .then(function(response2) {
                            //console.log(JSON.stringify(response2.data));
                            if (response2.data.success == true) {
                              //UPLOAD FILE TO MEDIA APP.
                              let fileFormData=new FormData();
                              fileFormData.append("file",file);
                               fileFormData.append("mediaType",mediaType);
                              const params = { crossDomain: true, withCredentials: true };
                              const apiX = axios.create({ baseURL: digitadApps.mediaFiles});
                               apiX
                                .post(
                                  digitadApps.storage + "/upload_campaign.php",
                                  fileFormData,
                                  options
                                )
                                .then((resultW) => {
                                  let feedBack=resultW.data;
                                  if (resultW.data.success == 1) {
                                      //STORE CAMPAIGN INFO IN DATABASE
                                      let mediaName=feedBack.file;
                                      //store campaign info in database
                                      let formData = new FormData();
                                      let data_param = JSON.stringify(campaignOBJ);
                                      formData.append("data", data_param);
                                      formData.append("file", file_name);
                                       formData.append("media_for_user",mediaName);
                                      formData.append("user_id", user_id);
                                      formData.append("email",email);
                                      formData.append("user_name", userName);
                                      const api = axios.create({ baseURL: digitadApps.users });
                                      api
                                        .post(
                                          digitadApps.users + "/server/submit_campaign.php",
                                          formData
                                        )
                                        .then((result) => {
                                          console.log(result);
                                          if (result.data == 1) {
                                            //RELO0AD PAGE
                                            location.reload();
                                          } else {
                                            //failed to update database alert message

                                            alert('failed to upload campaign')
                                          }
                                        })
                                        .catch((error) => {
                                          console.log(error);
                                        });
                                      ///update database end
                                  } 
                                })
                                .catch((error) => {
                                  console.log(error);
                                });
                              /*
                              
                              //UPLOAD FILE TO MEDIA APPLICATION
                              ///
                             */
                              ////////////////////
                            }
                          })
                          .catch(function(error) {
                            console.log(error);
                          });
                      }
                    })
                    .catch(function(error) {
                      console.log(error);
                    });
      }//end if
      
    
    },
    set_price() {
      this.campaignInfo.campaignCost =
        this.campaignInfo.multiplyConstant *
        parseInt(this.campaignInfo.numberOfDays);
      //set launched campaign currency
      this.campaignInfo.currency = this.currency;
      //console.log(this.campaignInfo.capaignCost);
    },
    sort_city() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.city == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
    },
    sortState() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.state == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
    },
    previewCampaignMedia() {
      let file = this.campaignInfo.campaignMedia;
      this.file = this.campaignInfo.campaignMedia;
      let fileType = file.type;
      if (
        fileType == "video/mp4" ||
        fileType == "video/webm" ||
        fileType == "video/ogg"
      ) {
        this.preview_campaign_media = true;
        this.previewMedia = URL.createObjectURL(this.file);
      }
    },
    validateFile() {
      this.errors = [];
      let file = this.campaignInfo.campaignMedia;
      //console.log(file);
      let fileSize = (file.size / (1024 * 1024)).toFixed(2);
      let fileType = file.type;
      let f_name = file.name;
      if (fileSize <= 50) {
        if (
          fileType == "video/mp4" ||
          fileType == "video/webm" ||
          fileType == "image/jpeg" ||
          fileType == "image/jpg" ||
          fileType == "image/png" ||
          fileType == "image/gif" ||
          fileType == "video/ogg"
        ) {
          this.campaignInfo.mediaType = fileType;
          this.campaignInfo.file_name = f_name;
          this.campaignInfo.fSize = fileSize;
        } else {
          this.errors.push({
            value: "INVALID FILE TYPE! ONLY :MP4,WEBM,JPG,PNG AND GIF ALLOWED",
          });
        }
      } else {
        this.errors.push({ value: "FILE TOO LARGE, MAX FILESIZE = 50MB" });
      }
      this.handleErrors();
    },
    calculateDays() {
      /*let campaignDateRange = this.campaignInfo.campaignDate;
      //console.log(campaignDateRange);
      this.return_date_array = campaignDateRange;
      let startDate = new Date(this.campaignInfo.campaignDate[0]);
      let formatedStartDate = startDate.toDateString();
      this.campaignInfo.startDate = formatedStartDate;
      let endDate = new Date(this.campaignInfo.campaignDate[1]);
      let formatedEndDate = endDate.toDateString();
      this.campaignInfo.endDate = formatedEndDate;
      let timeDifference = endDate.getTime() - startDate.getTime();
      let dateDifference = timeDifference / (1000 * 3600 * 24);
      this.campaignInfo.numberOfDays = dateDifference;*/
      this.set_price();
    },
    reloadMap() {
      this.mapKey = this.mapKey + 1;
      console.log("reloaded:" + this.mapKey);
    },
    reduce_price() {
      this.campaignInfo.multiplyConstant =
        this.campaignInfo.multiplyConstant -
        this.currentScreen.price_per_day;
    },
    price_calculator() {
      this.campaignInfo.multiplyConstant =
        this.campaignInfo.multiplyConstant +
        parseInt(this.currentScreen.price_per_day);
      //console.log(this.campaignInfo.numberOfDays);
    },
    selectAllDisplays() {
      if (this.checkbox == true) {
        //check all checkboxes
        for (let p = 0; p < this.checkboxStates.length; p++) {
          this.checkboxStates[p] = 1;
        }
        //set screens  to zero
        this.campaignInfo.numberOfScreens = 0;
        //increase number of screens
        this.campaignInfo.numberOfScreens = this.countryInfo.length;
        this.campaignInfo.campaignDisplays = this.countryInfo;
        //set price to zero
        this.campaignInfo.capaignCost = 0;
        this.countryInfo.forEach((screenInfo) => {
          let lat = screenInfo.latitude;
          let lng = screenInfo.longitude;
          let coords = { lat: parseFloat(lat), lng: parseFloat(lng) };
          this.map_coordinates.push(coords);
          //set current screen
          this.currentScreen = screenInfo;
          //call method to calculate price for current screen
          this.price_calculator();
        });
      } else {
        for (let p = 0; p < this.checkboxStates.length; p++) {
          this.checkboxStates[p] = 0;
        }
        //set screens  to zero
        this.campaignInfo.numberOfScreens = 0;
        //set price to zero
        this.campaignInfo.capaignCost = 0;
        this.campaignInfo.multiplyConstant = 0;
        this.map_coordinates = [];
      }
    },
    resetScreens() {
      if (this.checkbox == false) {
        //set screens  to zero
        this.campaignInfo.numberOfScreens = 0;
        //set price to zero
        this.campaignInfo.capaignCost = 0;
        this.map_coordinates = [];
      }
    },
    setScreens(object_index) {
      if (this.checkboxStates[object_index] == true) {
        // this.checkMe=1;
        this.campaignInfo.numberOfScreens++;
        this.campaignInfo.campaignDisplays.push(this.countryInfo[object_index]);
        let lat = this.countryInfo[object_index].latitude;
        let lng = this.countryInfo[object_index].longitude;
        let coords = { lat: parseFloat(lat), lng: parseFloat(lng) };
        this.map_coordinates.push(coords);
        //set current screen
        this.currentScreen = this.countryInfo[object_index];
        //call method to calculate price for current screen
        this.price_calculator();
      } else {
        this.campaignInfo.numberOfScreens =
          this.campaignInfo.numberOfScreens - 1;
        this.campaignInfo.campaignDisplays.pop(this.countryInfo[object_index]);
        let lat = this.countryInfo[object_index].latitude;
        let lng = this.countryInfo[object_index].longitude;
        let coords = { lat: parseFloat(lat), lng: parseFloat(lng) };
        this.map_coordinates.pop(coords);
        //set current screen
        this.currentScreen = this.countryInfo[object_index];
        //call method to calculate price for current screen
        this.reduce_price();
      }
    },
    createCurrencySymbol(country) {
      let currency = "";
      //set currency symbol
      if (country == "Nigeria") {
        currency = "₦";
      } else if (country == "Cameroon") {
        currency = "XAF";
      }
      return currency;
    },
    getScreens() {
      console.log("get screens called");
      //set currency symbol
      if (this.campaignInfo.campaignCountry == "Nigeria") {
        this.currency = "₦";
      } else if (this.campaignInfo.campaignCountry == "Cameroon") {
        this.currency = "XAF";
      }
    },
    setScreenView(url){
      this.currentScreen=url;
      this.screenView=true;
    },
    setPremiumData(){
      let selectedCity=this.campaignInfo.premium_data.city;
      let cityIndex=this.premium_cities.indexOf(selectedCity);
      let premiumData=this.premium_data[cityIndex];
      console.log(premiumData)
      //set premium data props in campaign
      this.campaignInfo.premium_data.weekly_plan=parseInt(premiumData.weekly_plan);
      this.campaignInfo.premium_data.monthly_plan=parseInt(premiumData.monthly_plan);
      //set screens
      this.screensInfo.forEach(screen=>{
        if(screen.city.toLowerCase()==selectedCity.toLowerCase()){
          //push screen into premiumCityScreen
          this.premiumCityScreens.push(screen);
        }
      })

    },
    setPremiumPlan(){
      //set campaign premium prop
      this.campaignInfo.premium=true;
      let selectedPlan=this.campaignInfo.premium_data.plan;
      if(selectedPlan=='WEEKLY PLAN'){
        this.campaignInfo.premium_data.cost=this.campaignInfo.premium_data.weekly_plan;
        this.campaignInfo.campaignCost=this.campaignInfo.premium_data.weekly_plan;
      }else{
        this.campaignInfo.premium_data.cost=this.campaignInfo.premium_data.monthly_plan;
        this.campaignInfo.campaignCost=this.campaignInfo.premium_data.monthly_plan;
      }

    }
  },
