      "Aim 40% of the playing time of all selected displays",
    ],
    checkboxStates: [],
    countries: ["Cameroon"],
    currency: "",
    campaignFileError: 0,
    campaignInfo: {
      campaignSubmit: 1,
      campaignName: "",
      numberOfDays:30,
      campaignCountry: "",
      campaignDisplays: [],
      campaignMedia:"",
      campaignCapacity: "",
      campaignCost: 0,
      numberOfScreens: 0,
      multiplyConstant: 0,
      startDate: "",
      endDate: "",
      mediaType: "",
      currency: "",
      file_name: "",
      duration: 0,
      fSize: 0,
      premium:false,
      premium_data:{
        city:"",
        plan:"",
        cost:0,
        weekly_plan:0,
        monthly_plan:0
      }
    },
  }),

  computed: mapGetters([
    "activeCampaigns",
    "rejectedCampaign",
    "archivedCampaigns",
    "unApprovedCampaigns",
    "not_paid_campaigns",
    "dateRangeText",
    "userDetails",
    "return_date_array",
    "screensInfo",
    "screenCities",
    "screenStates",
    "screen_checkboxStates",
    "wallet",
     "premiumCityScreens",
    "premium_data"
  ]),
  created() {
    
  },
  mounted() {
    let data = this.screensInfo;
    this.countryInfo = data;
    this.reservedCountryInfo = data;
    this.cities = this.screenCities;
    this.states = this.screenStates;
    this.checkboxStates = this.screen_checkboxStates;
  },
};
</script>
